<template>
  <div class="params">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="库存组织" size="mini">
            <erpCompanyList v-model="params.company_id" :fatherParams="params"></erpCompanyList>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable @focus="DepartmentListInvok(params)">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务员" size="mini">
            <el-select clearable v-model="params.business_id" @focus="EmployeeListInvok" filterable>
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据类型" size="mini">
            <el-select v-model="params.order_type" placeholder="请选择">
              <el-option label="全部" :value="0"></el-option>
              <el-option label="良品转不良品" :value="1"></el-option>
              <el-option label="不良品转良品" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.conversion_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="AuditDate" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="单据状态" size="mini">
            <el-select v-model="params.auditor_status" clearable placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="来源" size="mini">
            <el-select clearable v-model="params.form_type">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="WMS" :value="1">WMS</el-option>
              <el-option label="ERP" :value="2">ERP</el-option>
            </el-select>
          </el-form-item>

          <el-form-item size="mini" class="search-style">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet" >
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="NewList" class="addBtn" size="mini" v-if="authority.b_add == 1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del == 1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="audit" size="mini" v-if="authority.b_pass == 1">审核</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="abolish" class="addBtn" size="mini" v-if="authority.b_invalid == 1">作废</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" @click="unAudit" size="mini" v-if="authority.b_nopass == 1">
          弃审</el-button>
      </div>
      <div class="table_container">
        <xTable :ajax="{ url: 'DefectiveProductDefectiveProduct', params: params }" ref="xTable" :tableColumn="tableColumn" :xData="tableData" />
      </div>
    </div>
  </div>
</template>

<script>
import { TableHead, paramsModule } from "./config/Incomplete.js";
import { CompanySwitchClear } from "common/utils/public_function/commonUtils";
import { ThisScope } from "@/utils/commonCrud/CRUD.js";
export default {
  name: "Incomplete",
  mixins: [TableHead],
  data() {
    return {
      params: this.$publicFution.deepCopy(paramsModule),
      isShowBtn: true,
      AuditDate: [],
      timeValue: []
    };
  },
  methods: {
    companyChange() {
      CompanySwitchClear(this, this.params);
    },
    // 审核
    audit() {
      var obj = new ThisScope({
        id: "conversion_id",
        _this: this,
        title: "审核",
        url: "ConversionsAuditorPassOrder",
        showConfirm: true
      });
      obj.send();
    },

    // 弃审
    unAudit() {
      var obj = new ThisScope({
        id: "conversion_id",
        _this: this,
        title: "弃审",
        url: "ConversionsAntiAuditorOrder",
        showConfirm: true
      });
      obj.send();
    },
    // 作废
    abolish() {
      var obj = new ThisScope({
        id: "conversion_id",
        _this: this,
        title: "作废",
        url: "ConversionsInvalidOrder",
        showConfirm: true
      });
      obj.send();
    },
    // 删除
    deletes() {
      var obj = new ThisScope({
        id: "conversion_id",
        _this: this,
        title: "删除",
        url: "ConversionsDelOrder",
        showConfirm: true
      });
      obj.send();
    },
    LookOver(row) {
      this.$router.push({
        path: "/inventory/Incomplete/IncompleteSee",
        query: {
          conversion_id: row.conversion_id
        }
      });
    },
    NewList() {
      this.$router.push({
        path: "/inventory/Incomplete/IncompleteNew"
      });
    },
    modify(row) {
      this.$router.push({
        path: "/inventory/Incomplete/IncompleteNew",
        query: {
          conversion_id: row.conversion_id
        }
      });
    },
    onReset() {
      this.timeValue = [];
      this.AuditDate = [];
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.onSearch();
    },
    onSearch() {
      if (this.AuditDate) {
        this.params.start_auditor_time = this.AuditDate[0];
        this.params.end_auditor_time = this.AuditDate[1];
      } else {
        this.params.start_auditor_time = this.params.end_auditor_time = "";
      }
      if (this.timeValue) {
        this.params.start_business_time = this.timeValue[0];
        this.params.end_business_time = this.timeValue[1];
      } else {
        this.params.start_business_time = this.params.end_business_time = "";
      }
      this.$refs.xTable._getData();
    },
    // getBtnPower() {
    //   this.$axMethod.ConversionsListBtnPower().then(({ data }) => {
    //     if (data.code == 200) {
    //       if (data.data.success !== "ok") {
    //         this.isShowBtn = false;
    //       }
    //     }
    //   });
    // }
  },
  // created() {
  //   this.getBtnPower();
  // },
  activated() {
    this.onSearch(window.goPage);
  }
};
</script>
